<template>
  <div>
    <a-card id="login" title=''>
      <div >
        <a-input class="input-line"
          placeholder="username" 
          size='large' 
          v-model="username"
          @change="change"
        >
          <span slot="addonBefore"></span>
        </a-input>
        <a-input-password class="input-line"
          placeholder="password" 
          size='large'
          v-model="password"
          @change="change"
          @focus="enableEnter"
          @blur="unableEnter"
        >
          <span slot="addonBefore"></span>
        </a-input-password>
        <a-button style="margin-top: 10px; width: 100%;" 
          type='primary'
          @click="login"
        >
          
        </a-button>
      </div>
    </a-card>
  </div>
</template>

<script>
import {Card, Input, Button} from 'ant-design-vue'

export default {
  components: {
    ACard: Card,
    AInput: Input,
    AInputPassword: Input.Password,
    AButton: Button
  },
  data() {
    return {
      username: null,
      password: null
    }
  },
  methods: {
    change() {
      // console.log(`device name: ${this.username}, device id: ${this.password}`)
    },
    login() {
      const ip = this.$store.state.serverIP
      const router = ip + '/cam/login'
      this.axios.post(router, this.loginForm)
        .then(response => {
          const data = response.data
          const userInfo = data.data
          console.log(userInfo)
          this.getRefresh()
          this.$store.commit('updateUsername', userInfo.username)
          this.$router.push({name: 'overview'})
        })
        .catch(err => {
          err
          // console.log(err)
          // console.log('login fail')
        })
    },
    enableEnter() {
      window.onkeypress = e => {
        // console.log(e)
        if(e.keyCode === 13) {
          this.login()
        }
      }
      // console.log('enable')
    },
    unableEnter() {
      window.onkeypress = null
      // console.log('unable')
    },
    getRefresh() {
      const ip = this.$store.state.serverIP
      const getRouter = ip + '/cam/refresh'
      this.axios.get(getRouter + this.usernameParam)
        .then(response => {
          console.log(response)
          const data = response.data
          if(data.code === 200) {
            this.$store.commit('refresh', data.data)
          }
        })
    }
  },
  computed: {
    loginForm() {
      return {
        username: this.username, 
        password: this.password
      }
    },
    usernameParam() {
      return '?username=' + this.$store.state.username
    }
  }
}
</script>

<style>
#login {
  width: 40%;
  margin: 20px auto 0;
}

.input-line {
  margin: 20px 0;
}
</style>